"use client";

import { useState } from "react";

const UNIT_TYPES = [
  "Apartment",
  "Townhouse",
  "Penthouse",
  "Villa",
  "Mansion",
  "Plot",
];

const BUY_TIMELINE = [
  "I'm ready to buy",
  "Within 1 month",
  "Within 3 months",
  "Within 6 months",
];

export default function ConsultationForm() {
  const [loading, setLoading] = useState(false);
  const [submitted, setSubmitted] = useState(false);

  async function onSubmit(e) {
    e.preventDefault();
    setLoading(true);

    const form = new FormData(e.currentTarget);

    const payload = {
      fullName: form.get("fullName"),
      email: form.get("email"),
      phone: form.get("phone"),
      unitType: form.get("unitType"),
      buyTimeline: form.get("buyTimeline"),
      message: form.get("message"),
    };

    try {
      const res = await fetch("/api/contact", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = await res.json();

      if (res.ok && data.success) {
        setSubmitted(true);
        e.currentTarget.reset();
      }
    } catch (err) {
      console.error("Network error:", err);
      // Silently fail - don't show error to user
    } finally {
      setLoading(false);
    }
  }

  if (submitted) {
    return (
      <div style={successContainer}>
        <div style={successIcon}>✓</div>
        <div style={successTitle}>Thank You!</div>
        <div style={successMessage}>
          We have received your request and will contact you within 24 hours.
        </div>
        <button onClick={() => setSubmitted(false)} style={submitAnother}>
          Submit Another Request
        </button>
      </div>
    );
  }

  return (
    <form onSubmit={onSubmit} style={formWrap}>
      {/* HEADER */}
      <div style={head}>
        <div style={title}>Book your consultation now</div>
        <div style={titleAr}>احجز استشارتك الآن</div>
      </div>

      <div style={divider} />

      {/* FULL NAME */}
      <Label text="Your Full Name | الاسم الكامل" />
      <input name="fullName" required style={input} />

      {/* EMAIL */}
      <Label text="Email | بريدك الإلكتروني" />
      <input name="email" type="email" required style={input} />

      {/* PHONE */}
      <Label text="Phone Number | رقم موبايلك" />
      <input name="phone" required style={input} />

      {/* UNIT TYPE */}
      <Label text="Unit type you're looking to buy? | نوع الوحدة التي تريد شرائها" />
      <select name="unitType" required style={input}>
        <option value="">Select</option>
        {UNIT_TYPES.map((t) => (
          <option key={t}>{t}</option>
        ))}
      </select>

      {/* TIMELINE */}
      <Label text="When are you planning to buy? | متى تخطط للشراء" />
      <select name="buyTimeline" required style={input}>
        <option value="">Select</option>
        {BUY_TIMELINE.map((t) => (
          <option key={t}>{t}</option>
        ))}
      </select>

      {/* MESSAGE */}
      <Label text="Your Message | رسالتك" />
      <textarea name="message" required rows={5} style={textarea} />

      <div style={note}>We assure that your data will be protected.</div>

      {/* BUTTON */}
      <button type="submit" disabled={loading} style={submit}>
        {loading ? "SUBMITTING..." : "SUBMIT"}
      </button>
    </form>
  );
}

/* ================== SMALL COMPONENT ================== */

function Label({ text }) {
  return (
    <label style={label}>
      {text} <span style={{ color: "#ff5a5a" }}>*</span>
    </label>
  );
}

